import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthGuard } from 'src/app/core/guards/auth.guard';
import { BlankTagEntryManageComponent } from './components/blank-tag-entry/blank-tag-entry-manage/blank-tag-entry-manage.component';
import { PreparePhysicalCountingManageComponent } from './components/prepare-physical-counting/prepare-physical-counting-manage/prepare-physical-counting-manage.component';
import { PhysicalCountingComponent } from './physical-counting.component';
import { MobileCountManageComponent } from './components/mobile-count/mobile-count-manage/mobile-count-manage.component';
import {InitiateCountManageComponent} from './components/initiate-count/initiate-count-manage/initiate-count-manage.component';
import { TagEntryManageComponent } from './components/tag-entry/tag-entry-manage/tag-entry-manage.component';
import { AdjustPhysicalCountComponent } from './components/adjust-physical-count/adjust-physical-count.component';
import { PreparePhysicalCountExcelManageComponent } from './components/prepare-physical-count-excel/prepare-physical-count-excel-manage/prepare-physical-count-excel-manage.component';
import { InitiateCount2ManageComponent } from './components/initiate-count2/initiate-count2-manage/initiate-count2-manage.component';
import { ImportCountedExcelComponent } from './components/import-counted-excel/import-counted-excel/import-counted-excel.component';
import { ConfirmPhysicalCountManageComponent } from './components/confirm-physical-count/confirm-physical-count-manage/confirm-physical-count-manage.component';
import { ProcesscancelPhysicalCountingOrdersManageComponent } from './components/processcancel-physical-counting-orders/processcancel-physical-counting-orders-manage/processcancel-physical-counting-orders-manage.component';
import { MobileCount2ManageComponent } from './components/mobile-count2/mobile-count2-manage/mobile-count2-manage.component';


const routes: Routes = [
  {
      path: '',
      component: PhysicalCountingComponent,
      children: [
        { path: '', redirectTo: 'blank-tag-entry', pathMatch: 'full' },
        { path: 'blank-tag-entry/manage', component: BlankTagEntryManageComponent, canActivate: [AuthGuard] },
        { path: 'prepare-physical-counting/manage', component: PreparePhysicalCountingManageComponent, canActivate: [AuthGuard] },
        { path: 'mobile-count/manage', component: MobileCountManageComponent, canActivate: [AuthGuard] },
        { path: 'mobile-count2/manage', component: MobileCount2ManageComponent, canActivate: [AuthGuard] },
        { path: 'initiate-count/manage', component: InitiateCountManageComponent, canActivate: [AuthGuard] },
        { path: 'tag-entry/manage', component: TagEntryManageComponent, canActivate: [AuthGuard] },
        { path: 'adjust-physical-count', component: AdjustPhysicalCountComponent, canActivate: [AuthGuard] },
        { path: 'prepare-physical-count-excel/manage', component: PreparePhysicalCountExcelManageComponent, canActivate: [AuthGuard] },
        { path: 'initiate-count2/manage', component: InitiateCount2ManageComponent, canActivate: [AuthGuard] },
        { path: 'import-counted-excel', component: ImportCountedExcelComponent, canActivate: [AuthGuard] },
        { path: 'confirm-physical-count/manage', component: ConfirmPhysicalCountManageComponent, canActivate: [AuthGuard] },
        { path: 'processcancel-physical-counting-orders/manage', component: ProcesscancelPhysicalCountingOrdersManageComponent, canActivate: [AuthGuard] },
      ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PhysicalCountingRoutingModule { }
