import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { Observable } from 'rxjs';
import { HttpHeaders } from '@angular/common/http';
import { HttpClient, HttpBackend } from '@angular/common/http';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { Globals } from 'src/app/Global';
import * as XLSX from 'xlsx';

@Injectable({
  providedIn: 'root'
})
export class PreparePhysicalCountingService {
  private httpClient: HttpClient;
  public wsname: string = '';
  public wb: XLSX.WorkBook;
  public ws: XLSX.WorkSheet;

  constructor(
    private httpExt: HttpCleintExtService,
    private appSettings: AppSettingsService,
    private handler: HttpBackend,
    private lsService: LocalStorageService,
    private globals: Globals,
  ) {
    this.httpClient = new HttpClient(handler);
  }

  public bmlfonGet(reqdata): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpointnew}/bme/GetPreparePhysicalCount`;
    return this.httpExt.post<any>(url, reqdata, null);
  }

  public bmlfonSave(reqdata): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpointnew}/bme/SavePreparePhysicalCount`;
    return this.httpExt.post<any>(url, reqdata, null);
  }

  public getReportParameterValue(screenName, param) {
    let json = { 'ScreenName': screenName, 'ParametreValue': param }
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetReportParameterValue`;
    return this.httpExt.post(url, json, null);

  }
  public saveReportParameters(controls, header) {
    let oData: any = {};
    oData.HeaderData = [...header];
    oData.Controls = [...controls];
    oData.Response = [];
    oData.Response.push({ "ResponseText": "", "ErrorCode": "" });
    let jData = JSON.stringify(oData);
    let tempjData = JSON.stringify(jData);
    let json = '{"CustomToken":' + tempjData + '}';
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/SaveCustomParameter`;
    return this.httpExt.post(url, json, null);
  }
  public DeleteReportParameter(screenname, ParaValue) {
    let json = { 'ScreenName': screenname, 'ParametreValue': ParaValue }
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteCustomParameters`
    return this.httpExt.post(url, json, null)

  }

  public getReportParameter(screenName) {
    let json = { 'ScreenName': screenName }
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetReportParameter`;
    return this.httpExt.post(url, json, null);
  }

  // Prepare Physical Count Excel
  public bmlfonProcessPrePhyCountExcel(reqdata): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpointnew}/bme/BMGFProcessPrepPhyCountExcel`;
    return this.httpExt.post<any>(url, reqdata, null);
  }

  // Import Counted Excel
  public bmlfonUploadCountedExcel(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/UploadDocument`
    return this.httpExt.post<any>(url, reqdata,  {Anonymous: '' });
  }

  public bmlfonImportCountedExcel(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFImportCountedExcel`
    return this.httpExt.post<any>(url, reqdata,  {Anonymous: '' });
  }  

  // Confirm Physical Count
  public bmlfonGetConfirmPhysicalCount(reqdata): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpointnew}/bme/GetConfirmPhysicalCount`;
    return this.httpExt.post<any>(url, reqdata, null);
  }

  public bmlfonSaveConfirmPhysicalCount(reqdata): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpointnew}/bme/SaveConfirmPhysicalCount`;
    return this.httpExt.post<any>(url, reqdata, null);
  }

  // Process/Cancel Physical Counting Orders
  public bmlfonGetProcessPhysicalCount(reqdata): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpointnew}/bme/GetProcessPhysicalCount`;
    return this.httpExt.post<any>(url, reqdata, null);
  }
  
  public bmlfonSaveProcessPhysicalCount(reqdata): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpointnew}/bme/SaveProcessPhysicalCount`;
    return this.httpExt.post<any>(url, reqdata, null);
  }

  public exportAsExcelFile(json: any[], excelFileName: string): void {
    let worksheet = XLSX.utils.json_to_sheet(json);
    worksheet['!cols'] = [];
    if (this.bmgffindString(excelFileName, 'NonTrack')) {
      worksheet['!cols'][6] ={'hidden': true};         
      
    } else{
      worksheet['!cols'][11] ={'hidden': true};       
    }

    
   
    //worksheet.set_column('L', {'hidden': true});
    const workbook: XLSX.WorkBook = { Sheets: { 'data': worksheet }, SheetNames: ['data'] };
    const excelBuffer: any = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
    XLSX.writeFile(workbook, excelFileName);
  }

  bmgffindString(MainStr: any, pssubStr: any): boolean {   
   return MainStr.toLowerCase().includes(pssubStr.toLowerCase());
}


  public ImportExcelToJson(fileEvent) {
    let parrSheet: any = [];
    let workBook = null;
    let jsonData = null;
    let psData: any;
    let data = []
    let psSheetName = 'Sheet1';
    const reader = new FileReader();
    const file = fileEvent.target.files[0];

    let x = document.getElementById("txtfilePath");
    reader.readAsBinaryString(file);

    return new Promise((resolve, reject) => {
      reader.onload = (e: any) => {
        try {
          const binarystr: string = e.target.result;
          this.wb = XLSX.read(binarystr, { type: 'binary' });
          this.wsname = this.wb.SheetNames[0];
          this.wb.SheetNames.forEach(ele => {
            parrSheet.push({
              SheetName: ele,
              sheetPath: file.name
            })
          })
          this.ws = this.wb.Sheets[this.wsname];
          const data = XLSX.utils.sheet_to_json(this.ws);
          resolve(data);
        } catch (error) {
          resolve(reject);
        }
      };
    })
  }
}
