import { Component, OnInit } from '@angular/core';
import { CommonConstants } from 'src/app/core/constants/common.const';
import { LookupService } from 'src/app/shared/http/lookup.service';
import { ModalService } from 'src/app/core/services/modal.service';
import * as moment from 'moment';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { SelectableSettings, SelectAllCheckboxState } from '@progress/kendo-angular-grid';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { BaseGrid } from 'src/app/shared/components/basegrid';
import { Router } from '@angular/router';
import { PreparePhysicalCountingService } from '../../../http/prepare-physical-counting.service';
import { InputModel, RptParameterMap, ControlMap } from '../../../models/prepare-physical-counting.model';
import { PPCConstants } from '../../../constant/prepare-physical-counting.const';

@Component({
  selector: 'app-prepare-physical-counting-manage',
  templateUrl: './prepare-physical-counting-manage.component.html',
  styleUrls: ['./prepare-physical-counting-manage.component.scss']
})
export class PreparePhysicalCountingManageComponent extends BaseGrid implements OnInit  {
  public getDataList(): void {}
  public gridDataList: any;
  public mobjUserData: any = {};
  public mbLoaderEnabled:boolean = false;
  public mbPageLoading:boolean = false;
  public mbIsTaskModal: boolean = false;
  public mobjTaskData: any = {};
  public msScreenName: string = 'Prepare For Physical Counting';
  public msParmScreenName: string = 'Select Items to take in Physical Counting';
  public mbIsChange: boolean = false;
  public mbIsCloseMode: boolean = false;
  public mbDeleteDialogView: boolean = false;
  public mbSaveDialogView: boolean = false;
  public mbIsLoading: boolean = false;
  public marrLineGridData: any = [];
  public msRecordId;
  public mbEditMode = false;
  public msFormMode = 'I';
  public msCopyMode = 'N';
  public mobjSelectedItemData: any = {};
  public mobjSelectableSettings: SelectableSettings;
  public mbCheckboxOnly = false;
  public mode: any = 'single';
  public bmlfonSelectableSettings(): void {
    this.mobjSelectableSettings = {
      checkboxOnly: this.mbCheckboxOnly,
      mode: this.mode
    };
  }
  public mbIsSaveDone: boolean = false;
  public mbIsAddMode: boolean = false;
  public mbIsSearchMode: boolean = false;
  public mbIsDeleteMode: boolean = false;
  public mbIsLookupModal: boolean = false;
  public mobjLookupKeyData: any = {};
  public mbMutiSelectionLookup: boolean = false;
  public marrLookupGridData: any = [];
  public msCustomDateFormat: string = "";
  public marrCommonRecordSet: any = [];
  public mobjCommonDialogData: any = {};
  public mbCommonDialogView: boolean = false;
  public marrSelectedRecord:any = [];
  public marrSelectedData: any;
  public mbBulkResponseMsg: boolean = false;
  public msServeDateFormat = CommonConstants.gsServerDateFormat;
  public mbBulkResponseView: boolean = false;
  public msMsgData: any = {};
  public msFocusField = '';
  public msLookupField = '';
  public paramValueArray = [];
  public msscreenModeUpdate = "I";
  public mbcheckval = false;
  public mobjOrderModel: InputModel = new InputModel();
  public mobjOrderModelBlank: InputModel = new InputModel();
  public parameterValue: string = '';
  public marrLoadLookupFields: any = PPCConstants.garrLookupFieldsMap;
  public marrCountingType:any = ['Physical', 'Cycle']
  public marrItemList: any = PPCConstants.garrItemListMap;
  public garrLotsItemListMap: any = PPCConstants.garrLostItemListMap;
  public marrLotsItemGridData: any = [];
  public mobjLotsItemsData: any = {};
  public marrUomUnit: any = [];
  public mbIsItemModal: boolean = false;
  public autoCorrect = true;
  public miQtyPrecision: number = 0;
  public miQtyPrecisionFormat: string = 'n'+this.miQtyPrecision;
  public QtyMaxLen = 12;
  public msModalTitle = '';
  public mobjHeaderRec: any = [];
  public mbProcessMsgDialog: boolean = false;
  public msProcessReportInfo: any;

  public msGridNameItem = 'DTItem';
  public msGridNameLot = 'DTLot';

  constructor(
    private lookupService: LookupService,
    private modalservice: ModalService,
    public dateFormatService: DateFormatService,
    private lsService: LocalStorageService,
    public utilService: AppUtility,
    public toastMessageService: ToastMessageService,
    public userFieldService: UserFieldService,
    private router: Router,
    public countingService: PreparePhysicalCountingService
  ) {
    super(lsService, dateFormatService, userFieldService, toastMessageService, utilService);
  }

  ngOnInit(): void {
    this.utilService.getBMEScreePermission(this.msScreenName); // Fetch Screen permission
    this.mobjUserData = this.localStorageService.getUserSession();
    this.getParam(this.msParmScreenName);
    this.mobjOrderModel.CountingType = this.marrCountingType[0];
    this.bmlfGetPrecision();
    this.SetGridColumns(this.msGridNameItem, this.marrItemList);  
    this.SetGridColumns(this.msGridNameLot, this.garrLotsItemListMap);  
  }

  bmlfGetPrecision(){
    try{
      let data = JSON.parse(localStorage.getItem('RoundingDefaults'));
      this.miQtyPrecision = data[0]["Default_Value"];
      this.miQtyPrecisionFormat = 'n'+ this.miQtyPrecision.toString()
    }
    catch(e){
      this.miQtyPrecision =2;
      this.miQtyPrecisionFormat = 'n'+ this.miQtyPrecision.toString()
    }
  }

  // common Action Event
  bmlfCommonDialogAction(psAction) {
    if (psAction == 'Cancel' || psAction == 'No') {
      this.mbCommonDialogView = false;
    } else if (psAction == 'deleteEvent') {
      this.mbCommonDialogView = false;
    }
  }

  bmlfCommonAction(pobjData, type) {
    let parrRecordData:any = [];
    this.marrCommonRecordSet = [];
    this.marrSelectedData = [];
    if (!Array.isArray(pobjData)) {
      this.mbBulkResponseMsg = false;
      parrRecordData.push(pobjData);
    } else {
      this.mbBulkResponseMsg = true;
      parrRecordData = pobjData;
    }
    this.marrSelectedData = parrRecordData;

    this.mobjCommonDialogData = {};
    this.mbCommonDialogView = true;
  }

  bmlfonConfirmModal(psype) {
    if (psype == 'yes') {
      this.bmlfOnSave();
      this.mbSaveDialogView = false;
    } else if (psype == 'no') {
      this.mbSaveDialogView = false;
      this.bmlfonPageRefresh();
    } else {
      this.mbSaveDialogView = false;
      this.mbIsCloseMode = false;
    }
  }

  // Form Action finction
  bmlfFormActionFn(event) {
    if (event == 'processEvn') {
      this.bmlfOnProcessRec();
    }
  }

  //Open LOOKUP MODAL
  bmlfonOpenLookupModal(psField, psKey, modelName, psEvent, psValue, psType, psEleId, piRowIdx = null) {
    if (psEvent == 'blur' && this.msLookupField == psValue) {
      this.msLookupField = '';
      return false;
    }
    this.mbMutiSelectionLookup = false;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};

    if (psEvent == 'blur' && (psValue == undefined || psValue == '')) {
      this.mobjOrderModel[psKey + 'Des'] = '';
      return false
    }
    let psLookUpKey = psField;
    let psScreenName = this.msScreenName;
    let pobjReqData: any = {
      ScreenName: psScreenName
    }
    pobjReqData.LookUpKey = psLookUpKey;
    this.utilService.bmgfOnKeyInputDisable();
    this.mbIsLoading = true;
    this.lookupService.getLookup(pobjReqData).subscribe(res => {
      setTimeout(() => {
        this.utilService.bmgfOnKeyInputEnable();
        this.mbIsLoading = false;
      }, 500);
      if (res == null) {
        if (psEleId) {
          let elementExists = document.getElementById(psEleId);
          if (elementExists) {
            document.getElementById(psEleId).focus();
          }
        }
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.MSG.NODATA'));
        return false;
      }
      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
        res.LookupData.forEach((ele) => {
          Object.keys(ele).forEach((key) => {
            let psKey = key.toUpperCase();
            if (psKey.search('DATE') != -1) {
              if (ele[key] && ele[key] != '') {
                ele[key] = moment(ele[key]).format(this.msCustomDateFormat.toUpperCase());
              } else {
                ele[key] = moment().format(this.msCustomDateFormat.toUpperCase());
              }
            }
          })
        })
        this.marrLookupGridData = res.LookupData;
        let title = psField;
        title = this.utilService.bmgfonTranslate('PREPAREPHYSICALCOUNTING.' + psField.toUpperCase());
        let updatedtitle;
        if (title.includes('From')) {
          updatedtitle = title.replace('From', '')
        } else if (title.includes('To')) {
          updatedtitle = title.replace('To', '')
        }
        // this.miLineItemRowIndex = piRowIdx;
        this.mobjLookupKeyData.field = psField;
        this.mobjLookupKeyData.key = psKey;
        this.mobjLookupKeyData.title = updatedtitle;
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.controlName = modelName;
        this.mobjLookupKeyData.eleId = psEleId;
        // this.mobjLookupKeyData.qtyDecimalLength = this.miQtyPrecision;
        // this.mobjLookupKeyData.qtyDecimalFormat = this.miQtyPrecisionFormat;
        if (psValue) {
          this.mobjLookupKeyData.searchValue = psValue;
        } else {
          this.mobjLookupKeyData.searchValue = '';
        }
        if (psEvent == 'blur' && psValue) {
          let psInputKey = this.bmlfGetInputColumn(psKey);
          let parrFilterData: any = [];
          parrFilterData = this.marrLookupGridData.filter(obj => {
            let tempValue = obj[psInputKey] == null || obj[psInputKey] == '' ? '' : obj[psInputKey].toUpperCase();
            if (tempValue == psValue.toUpperCase()) {
              return obj;
            }
          });
          console.log(parrFilterData);
          if (parrFilterData.length == 1) {
            this.mobjLookupKeyData.value = parrFilterData[0]
            this.bmlfonSaveLookupField(this.mobjLookupKeyData);
            this.mbIsLookupModal = false;
          } else if (parrFilterData.length == 0) {
              let elementExists = document.getElementById(psEleId);
              if (elementExists) {
                document.getElementById(psEleId).focus();
              }
              this.mbIsLookupModal = true;
          }
        } else if (psEvent == 'click') {
          this.mbIsLookupModal = true;
        }
      } else {
        if (psEleId) {
          let elementExists = document.getElementById(psEleId);
          if (elementExists) {
            document.getElementById(psEleId).focus();
          }
        }
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.MSG.NODATA'));
      }
    }, err => {
      console.log(err);
    });
  }

  bmlfonSaveLookupField(pobjData) {
    let psInputKey = this.bmlfGetInputColumn(pobjData.key);
    let psInputDesKey = this.bmlfGetInputDesColumn(pobjData.key);
    let validationFlag = this.bmlfOnValidateControls(pobjData.key, pobjData.value[psInputKey]);
    if (validationFlag == true) {
      this.mobjOrderModel[pobjData.key] = pobjData.value[psInputKey];
      if (psInputDesKey) {
        this.mobjOrderModel[pobjData.key + 'Des'] = pobjData.value[psInputDesKey];
      }
    } else {
      this.mobjOrderModel[pobjData.key] = '';
      this.mobjOrderModel[pobjData.key + 'Des'] = '';
      let elementExists = document.getElementById(pobjData.eleId);
      if (elementExists) {
        document.getElementById(pobjData.eleId).focus();
      }
    }
    this.mbIsLookupModal = false;
    this.mbMutiSelectionLookup = false;
  }

  bmlfOnValidateControls(ctrlName, value): Boolean {
    let psValue = value.toUpperCase();
    let psCtrlValue = this.mobjOrderModel[ControlMap[ctrlName]].toUpperCase();
    if (ctrlName.includes("From") == true) {
      if (this.mobjOrderModel[ControlMap[ctrlName]] == "") {
        return true
      } else if (psValue > psCtrlValue) {//if (value > this.mobjOrderModel[ControlMap[ctrlName]]) {
        this.onOpenErrorBlock(this.utilService.bmgfonTranslate('PREPAREPHYSICALCOUNTING.VALMSG2'));
        return false
      } else {
        return true
      }
    } else {
      if (this.mobjOrderModel[ControlMap[ctrlName]] == "") {
        return true
      }
      else if (psValue < psCtrlValue) {//(value < this.mobjOrderModel[ControlMap[ctrlName]]) {
        this.onOpenErrorBlock(this.utilService.bmgfonTranslate('PREPAREPHYSICALCOUNTING.VALMSG2'));
        return false
      }
      else {
        return true
      }
    }
  }

  // Set Lookup Value Column
  bmlfGetInputColumn(psField) {
    if (psField == 'ItemFrom' || psField == 'ItemTo') {
      psField = 'Itemkey'
      return psField
    } else if (psField == 'LocFrom' || psField == 'LocTo') {
      psField = 'Location'
      return psField
    } else if (psField == 'AbcKeyFrom' || psField == 'AbcKeyTo') {
      psField = 'AbcKey'
      return psField
    } else if (psField == 'ProductKeyFrom' || psField == 'ProductKeyTo') {
      psField = 'ProductKey'
      return psField
    } else if (psField == 'InClassKeyFrom' || psField == 'InClassKeyTo') {
      psField = 'InClassKey'
      return psField
    } else if (psField == 'BinNoFrom' || psField == 'BinNoTo') {
      psField = 'BinNo'
      return psField
    }
  }

  // Set Lookup Description Column
  bmlfGetInputDesColumn(psField) {
    if (psField == 'ItemFrom' || psField == 'ItemTo') {
      return 'Desc1'
    } else if (psField == 'LocFrom' || psField == 'LocTo' || psField == 'AbcKeyFrom' || psField == 'AbcKeyTo' || psField == 'ProductKeyFrom' || psField == 'ProductKeyTo' || psField == 'InClassKeyFrom' || psField == 'InClassKeyTo' || psField == 'BinNoFrom' || psField == 'BinNoTo') {
      return 'Description'
    }
  }

  //Close LOOKUP MODAL
  bmlfonCloseLookupModal(pobjData) {
    this.mbIsLookupModal = pobjData.isLookupModal;
    if (pobjData.eleId != null) {
      let psInputKey = this.bmlfGetInputColumn(pobjData.field);
      let parrFilterData: any = [];
      if (psInputKey) {
        parrFilterData = pobjData.gridData.filter(obj => obj[psInputKey].toUpperCase() == pobjData.searchValue.toUpperCase());
      }
      // console.log(pos);
      if (parrFilterData.length == 0) {
        this.mobjOrderModel[pobjData.key] = '';
        this.mobjOrderModel[pobjData.key + 'Des'] = '';
      }
      let elementExists = document.getElementById(pobjData.eleId);
      if (elementExists) {
        document.getElementById(pobjData.eleId).focus();
      }
    }
  }

  //Open Task MODAL
  bmlfOpenTaskModal(taskType) {
    this.mobjTaskData = {};
    this.mobjTaskData.taskType = taskType;
    if (taskType == 'myTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('MYTASK');
      this.mobjTaskData.innerRecord = this.mobjUserData;
    } else if (taskType == 'recordTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('TASKFORRECORD');
      let pobjFormRec:any = {};
      pobjFormRec.formName = this.msScreenName;
      pobjFormRec.primeKey = '';
      this.mobjTaskData.innerRecord = pobjFormRec;
    }
    this.mbIsTaskModal = true;
  }

  //Close Task MODAL
  bmlfonCloseTaskModal(pobjData) {
    this.mbIsTaskModal = pobjData.isTaskModal;
  }

  // View Help Dcoumnet
  bmlfViewHelpDoc() {
    window.open('../../../../assets/help files/Prepare For Physical Counting.pdf', '_blank',);
  }

  // Close Modal
  bmlfonCloseModal(type) {
    if (this.mbIsItemModal) {
      if (type == 'screenClose') {
        if (this.mbIsChange) {
          this.mbIsCloseMode = true;
          this.mbSaveDialogView = true;
          return false;
        }
        this.bmlfonPageRefresh();
      } else if (type == 'LotDetails') {
        this.modalservice.close('LotDetails');
      }
      return false;
    }
    if (type == 'screenClose') {
      this.router.navigate(['/main/dashboard']);
    }
  }

  bmlfonCloseProcessReportModal() {
    this.msProcessReportInfo = "";
    this.mbProcessMsgDialog = false;
  }

  bmlfonParamchange(e) {
    try {
      let parametername = e.target.value;
      if(parametername.trim() != ''){
        if(this.paramValueArray.length>0){
          let list = this.paramValueArray.filter(x => x.PARANAME === parametername)[0];
          this.fetchParamData(list.PARANAME)
        }
      }
    }
    catch (e) {
      console.log(e);
      console.trace();
    }
  }
  private fetchParamData(param) {
    let pValue = param;
    this.mbIsLoading = true;
    this.countingService.getReportParameterValue(this.msParmScreenName, param).subscribe(res => {
      if (res != null && res.RPTPARAMETERSBYVALUE.length > 0) {
        this.bmlfclearModel();
        this.fillControlFromParam(res.RPTPARAMETERSBYVALUE);
      }
      else {
        console.error('No Response');
        console.trace();
        this.mbIsLoading = false;
      }
    })
  }

  bmlfclearModel() {
    this.mobjOrderModel = Object.assign({}, this.mobjOrderModel);
  }

  private fillControlFromParam(resData) {
    resData.forEach(element => {
      let key = this.bmlfongetKeyFromValue(element.CONTROLNAME);
      this.mobjOrderModel[key] = element.CONTROLVALUE;
    });
    let parrOptCycCount = resData.filter(i => i.CONTROLNAME == 'optCycCount' && i.CONTROLVALUE == 'True');
    if (parrOptCycCount.length) {
      this.mobjOrderModel['CountingType'] = this.marrCountingType[1];
    }
    let parrOptPhyCount = resData.filter(i => i.CONTROLNAME == 'optPhyCount' && i.CONTROLVALUE == 'True');
    if (parrOptPhyCount.length) {
      this.mobjOrderModel['CountingType'] = this.marrCountingType[0];
    }
    this.mbIsLoading = false;
  }

  bmlfongetKeyFromValue(value) {
    let newObj = this.mobjOrderModel;
    let tempKey = Object.keys(newObj).find(data => {
      return RptParameterMap[data] == value
    });
    return tempKey;
  }

  bmlfonParameterSave() {
    let paramValue = this.parameterValue; //this.paramCtrl.nativeElement.value;
    let newObj = this.mobjOrderModel;

    if (paramValue == '') {
      this.onOpenErrorBlock(this.utilService.bmgfonTranslate('PREPAREPHYSICALCOUNTING.PRMEMPTY'));
      return;
    } else {
      this.mbIsLoading = true;
      this.msscreenModeUpdate = "I";
      let jObj = {};
      let headerData = [];
      let jArray = [];
      let counterRecId = 0;
      //let oArray = [...this.dataListCtrl.nativeElement.options];
      jObj["ParamName"] = paramValue;
      jObj["ScreenName"] = this.msParmScreenName;
      if (this.paramValueArray.length>0){
        let list = this.paramValueArray.filter(x => x.PARANAME === paramValue)[0];
        if (list !=undefined){
          this.msscreenModeUpdate = 'U';
          jObj["UpdateMode"] = this.msscreenModeUpdate;
        } else{
          jObj["UpdateMode"] = this.msscreenModeUpdate;
        }
      } else {
        jObj["UpdateMode"] = this.msscreenModeUpdate;
      }

      headerData.push(jObj);
      Object.keys(newObj).map(key => {
        if (key != undefined && typeof (newObj[key]) == "string") {
          let tempJObj = {};
          if (RptParameterMap[key] != undefined) {
            tempJObj["ControlName"] = RptParameterMap[key];
            tempJObj["ControlValue"] = newObj[key];
            tempJObj["RecId"] = counterRecId++;
            jArray.push(tempJObj);
          }
        }
      });
      let pobjOptPhyCount: any = {
        ControlName: 'optPhyCount',
        ControlValue: this.mobjOrderModel.CountingType == this.marrCountingType[0] ? 'True' : 'False',
        RecId: jArray.length + 1
      };
      jArray.push(pobjOptPhyCount);
      let povjOptCycCount: any = {
        ControlName: 'optCycCount',
        ControlValue: this.mobjOrderModel.CountingType == this.marrCountingType[1] ? 'True' : 'False',
        RecId: jArray.length + 1
      };
      jArray.push(povjOptCycCount);
      console.log(jArray);
      this.countingService.saveReportParameters(jArray, headerData).subscribe(res => {
        if (res != null) {
          if (res['Response'][0].ResponseText == "Sucess") {
            //this.paramCtrl.nativeElement.value = "";
            this.parameterValue = '';
            this.mbIsLoading = false;
            this.getParam(this.msParmScreenName);
          }
          else {
            console.trace();
            console.error('Save error');
            this.mbIsLoading = false;
          }
        }
      })
    }

  }

  bmlfonParameterDelete() {
    let oParaValue = this.parameterValue ;//this.paramCtrl.nativeElement.value;
    if (oParaValue == "") {
      this.onOpenErrorBlock(this.utilService.bmgfonTranslate('PREPAREPHYSICALCOUNTING.SELRPTPRM'));
      return;
    }
    else {
      this.mbcheckval = false
      this.mbIsLoading = true;
      this.bmlfondeleteApiParam(oParaValue);
      this.parameterValue ='';
      return true;
    }
  }

  onOpenErrorBlock(msg) {
    this.toastMessageService.notifyError(msg);
  }

  bmlfondeleteApiParam(oParaValue) {
    this.countingService.DeleteReportParameter(this.msParmScreenName, oParaValue).subscribe(res => {
      if (res != null) {
        if (res.Response[0].ResponseText == "Success") {
          this.onOpenErrorBlock(this.utilService.bmgfonTranslate('PREPAREPHYSICALCOUNTING.DELMSG'));
         // this.paramCtrl.nativeElement.value = "";
          this.parameterValue ='';
          this.bmlfclearModel();
          this.getParam(this.msParmScreenName);
          this.mbIsLoading = false;
          return true;
        }
        this.mbIsLoading = false;
      }

    })
  }

  private getParam(scr) {
    this.mbIsLoading = true;
    this.countingService.getReportParameter(scr).subscribe(res => {
      if (res != null) {
        this.paramValueArray = res.RPTPARAMETERS;
        this.mbIsLoading = false;
      } else {
        console.log("Res Is Null");
        this.mbIsLoading = false;
      }
    })
  }

  // GlobalOnChanges Event
  bmlfonGlobalOnChangesFn(psFieldName, pobjFieldData, piIndex?, eleId?) {
    if (pobjFieldData == null) {
      return false;
    }
    if (this.mbIsItemModal) {
      this.mbIsChange = true;
      this.mbIsSaveDone = false;
    }
  }

  // Global focus Fn
  bmlfOnGlobalFocus(field, value) {
    this.msFocusField = field;
    if (typeof value === 'object' && value.constructor === Object) {
      let valueCopy = Object.assign({}, value);
      this.msLookupField = valueCopy[field];
    } else {
      this.msLookupField = value;
    }
  }

  // Global Blur Fn
  bmlfOnGlobalBlur(field, psValue, eleId) {
    this.msFocusField = '';
  }

  // update header data
  bmlfOnUpdateHeaderData(pobjData) {
    console.log(this.mobjOrderModel.CountingType);
    pobjData.CountingType = pobjData.CountingType == this.marrCountingType[1] ? true : false;
    delete pobjData.ItemFromDes;
    delete pobjData.ItemToDes;
    delete pobjData.LocFromDes;
    delete pobjData.LocToDes;
    delete pobjData.AbcKeyFromDes;
    delete pobjData.AbcKeyToDes;
    delete pobjData.ProductKeyFromDes;
    delete pobjData.ProductKeyToDes;
    delete pobjData.InClassKeyFromDes;
    delete pobjData.InClassKeyToDes;
    delete pobjData.BinNoFromDes;
    delete pobjData.BinNoToDes;
    delete pobjData['undefined']
    return pobjData;
  }

  // on process records
  bmlfOnProcessRec() {
    let pobjCopyRec = Object.assign({}, this.mobjOrderModel);
    let pobjFilterHeaderRec = this.bmlfOnUpdateHeaderData(pobjCopyRec);
    let pobjRecordSet: any = {
      HeaderData: [],
      Response: [{ ResponseText: '', 'ErrorCode': '' }]
    }
    if (this.mbIsItemModal) {
      pobjRecordSet.HeaderData = this.mobjHeaderRec;
    } else {
      pobjRecordSet.HeaderData.push(pobjFilterHeaderRec);
    }

    let pobjReqData: any = {
      JSON: JSON.stringify(pobjRecordSet)
    };
    this.mbIsLoading = true;
    this.countingService.bmlfonGet(pobjReqData).subscribe(res => {
      this.mbIsLoading = false;
      this.mbCommonDialogView = false;
      if (res == null) {
        this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('COMMON.MSG.SERVERERROR'));
        return false;
      }
      if (res.Response[0].ErrorCode == '') {
        this.mobjHeaderRec = res.HeaderData;
        if (res.ItemDetails.length) {
          this.marrUomUnit = [];
          res.UOMs.forEach(ele => {
            let uomObj: any = {};
            uomObj.ToKey = ele.ToKey;
            uomObj.FromKey = ele.FromKey;
            uomObj.convfctr = ele.convfctr == 0 ? 1 : ele.convfctr;;
            uomObj.Operation = ele.Operation;
            uomObj.Itemkey = ele.Itemkey;
            this.marrUomUnit.push(uomObj);
          });
          res.ItemDetails.map(ele => {
            ele.oldQtyonhand = ele.Qtyonhand;
            ele.oldUOMUnit = ele.Uomcode;
            let parrUOMData;
            if (res.LotDetails.length) {
              ele.LotData = res.LotDetails.filter(i => i.ItemKey == ele.Itemkey && i.Location == ele.Location);
            }
            parrUOMData = this.marrUomUnit.filter((val) => val.Itemkey == ele.Itemkey);
            setTimeout(() => {
              ele.uomUnit = this.utilService.bmgfRemoveDupObj(parrUOMData, 'ToKey');
            }, 200);
            this.mbIsItemModal = true;
          });
          this.marrLineGridData = [];
          this.marrLineGridData = res.ItemDetails;
          this.mbIsChange = false;
          if (this.mbIsCloseMode) {
            this.mbIsCloseMode = false;
            this.bmlfonPageRefresh();
          }
        } else {
          this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('PREPAREPHYSICALCOUNTING.NORECORDFORPC'));
          this.mbIsItemModal = false;
        }
      }  else if (res.Response[0].ErrorCode != '') {
        this.toastMessageService.notifyError(res.Response[0].ResponseText);
      }
    }, err => {console.log(err);});
  }

  // Refresh page
  bmlfonPageRefresh() {
    this.utilService.bmgfRefreshReuse('/main/physicalcounting/prepare-physical-counting/manage', false);
  }

  // Change UOM UNIT
  bmlfonUnitChangeFn(psFieldName, psKey, pobjLineData, piIndex) {
    
    let parrFilterIndex = this.marrLineGridData.filter(x=>{
      if(x.Itemkey ==  pobjLineData.Itemkey && x.Location == pobjLineData.Location){
        return x;
      }
    })
    let parrSelectedUnit = pobjLineData.uomUnit.filter((value) => value.ToKey == psKey);
    if (psFieldName == 'Uomcode') {
      let pioldQtyonhand = 0;
      pioldQtyonhand = pobjLineData.InvQty;
      let piQtyonhand;
      if (parrSelectedUnit[0].Operation == '2') {
        piQtyonhand = (1 / parrSelectedUnit[0].convfctr) * pioldQtyonhand;
        //this.marrLineGridData[piIndex].Qtyonhand = piQtyonhand ? piQtyonhand : pioldQtyonhand; // client issue
        parrFilterIndex[0].Qtyonhand = piQtyonhand ? piQtyonhand : pioldQtyonhand;
      } else {
        piQtyonhand = parrSelectedUnit[0].convfctr * pioldQtyonhand
       // this.marrLineGridData[piIndex].Qtyonhand = piQtyonhand
       parrFilterIndex[0].Qtyonhand = piQtyonhand
      }
      //this.marrLineGridData[piIndex].oldQtyonhand = this.marrLineGridData[piIndex].Qtyonhand;
      parrFilterIndex[0].oldQtyonhand = parrFilterIndex[0].Qtyonhand;
      parrFilterIndex[0].LotData.map(ele => {
        let pioldLotQtyonhand = 0;
        pioldLotQtyonhand = ele.LotInvQty;
        let piLotQtyonhand;
        if (parrSelectedUnit[0].Operation == '2') {
          piLotQtyonhand = (1 / parrSelectedUnit[0].convfctr) * pioldLotQtyonhand;
          ele.Qtyonhand = piLotQtyonhand ? piLotQtyonhand : pioldLotQtyonhand;
        } else {
          piLotQtyonhand = parrSelectedUnit[0].convfctr * pioldLotQtyonhand
          ele.Qtyonhand = piLotQtyonhand;
        }
      });
    }
  }

  bmlfonOpenModal(psId, pobjData, piIndex = null) {
    if (psId == 'LotDetails') {
      if (pobjData.seriallotflg == 'N') {
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('PREPAREPHYSICALCOUNTING.NONTRACKITEMMSG'));
        return false;
      }
      this.mobjLotsItemsData = {};
      this.mobjLotsItemsData = pobjData;
      this.msModalTitle = this.utilService.bmgfonTranslate('PREPAREPHYSICALCOUNTING.LOTDETAILS');
      this.marrLotsItemGridData = pobjData.LotData;
    }
    this.modalservice.open(psId);
  }
 
  NumberisNull(value) {
    if (value == null || value == undefined) {
      return 0;
    } else {
      return value
    }
  }

  bmlfOnSave() {
    if (!this.utilService.mbWrite){
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return false;
    };
    let parrCopyLineData = this.marrLineGridData.map(ele => Object.assign({}, ele));
    let parrSelectedLot: any = [];
    parrCopyLineData.map(ele => {
      if (ele.Sel == true) {
        ele.Qtyonhand =  Number(ele.Qtyonhand).toFixed(this.miQtyPrecision); 
        ele.InvQty = Number(ele.InvQty).toFixed(this.miQtyPrecision);  
        if (ele.LotData && ele.LotData.length) {
          ele.LotData.forEach(obj => {
            if (obj.Sel == true) {
              //Avanish 28Jul23 SCR:49368
              obj.LotInvQty =  Number(obj.LotInvQty).toFixed(this.miQtyPrecision); 
              obj.Qtyonhand = Number(obj.Qtyonhand).toFixed(this.miQtyPrecision);  
              obj.QtyActual = Number(obj.QtyActual).toFixed(this.miQtyPrecision);  
              obj.QtyAdjusted = Number(obj.QtyAdjusted).toFixed(this.miQtyPrecision);  
              parrSelectedLot.push(obj);
            }
          });
        }
      }
    });
    let parrItemData = [];
    parrCopyLineData.map(ele => {
      if (ele.Sel == true) {
        delete ele.LotData;
        delete ele.oldUOMUnit;
        delete ele.oldQtyonhand;
        delete ele.uomUnit;
        parrItemData.push(ele);
      }
    });
    if (parrItemData.length == 0) {
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.MSG.NODATASAVE'));
      return false;
    }
    let pobjRecordSet: any = {
      HeaderData: this.mobjHeaderRec,
      ItemDetails: parrItemData,
      LotDetails: parrSelectedLot,
      Response: [{ ResponseText: '', 'ErrorCode': '' }]
    }
    // pobjRecordSet.ItemDetails.push(this.mobjHeaderRec);
    let pobjReqData: any = {
      JSON: JSON.stringify(pobjRecordSet)
    };
    this.mbIsLoading = true;
    this.countingService.bmlfonSave(pobjReqData).subscribe(res => {
      this.mbIsLoading = false;
      this.mbIsChange = false;
      this.mbCommonDialogView = false;
      if (res == null) {
        this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('COMMON.MSG.SERVERERROR'));
        return false;
      }
      if (res.ItemDetails.length == 0) {
        // this.bmlfonPageRefresh();
        this.mbIsItemModal = false;
      } else {
        this.bmlfOnProcessRec();
      }
      // this.mbProcessMsgDialog = true;
      // this.msProcessReportInfo = res.Response[0].ResponseText;
      this.msMsgData.screenName = this.msScreenName;
      this.msMsgData.Data = res.Response[0].ResponseText;
      this.mbBulkResponseView = true;
    }, err => {console.log(err);});
  }

  // select deselect all
  bmlfonSelectDeSelect(type, psValue) {
    if (type == 'itemType') {
      this.marrLineGridData.forEach(ele=> {ele.Sel = psValue});
    } else if (type == 'lotType') {
      this.marrLotsItemGridData.forEach(obj => {
        obj.Sel = psValue;
      });
    }
  }

  bmlfOnRptAction(event) {
    this.mbBulkResponseView = false;
  }

  SetGridColumns(GridName, parrGridCol) {
    this.utilService.GetJsonSetting(this.msScreenName, this.mobjUserData.UserId, GridName, parrGridCol).then((pArrGridChangedCol: any) => {
      if (pArrGridChangedCol != null && pArrGridChangedCol != undefined) {
        parrGridCol = pArrGridChangedCol
      } 
    }, err => {
      console.log(err);     
    });
  }
}
