  bmlfonDialogAction(action) {
    debugger
    if (action == 'No') {
      this.mbShowDialog = false;
      this.mbChange = false;
      if (this.msCallFor == "FormClose") {
        this.router.navigate(['/main/dashboard/dashboardpage']);
      }else if (this.msCallFor == "Add") {
        this.bmlfonClearControl(false);
      }else if (this.msCallFor == "ChangeTag") {
        this.bmlfGetTag();
      }
      else {
        return;
      }

    } //Yes
    else if (action == 'Yes' && this.msCallFor == "FormClose") {
      this.bmlfonSubmitData();
    }else if (action == 'Yes' && this.msCallFor == "Add") {
      this.bmlfonSubmitData();
    }else if (action == 'Yes' && this.msCallFor == "ChangeTag") {
      this.bmlfonSubmitData();
    }
    else if (action == 'Yes') {
      // call update function
      this.mbChange = false;
      this.mbShowDialog = false;
      this.msFormMode = "U";
      this.mbDisabledPrimaryKey = true;
    } else if (action == 'Cancel' && this.msCallFor == "ChangeTag") {
      this.mbShowDialog = false;
      setTimeout(function () { document.getElementById('txtTagNo').focus(); }, 10);
      return false;
    }
    else {
      this.mbShowDialog = false;
    }
  }

  

