onblurTag(){
  if (this.mbChange) {
    this.msCallFor = "ChangeTag";
    this.mbShowDialog = true;
    return false;
  }else{
    this.bmlfGetTag();
  }
}

bmlfGetTag() {
    this.isShowExtender = true;
          if(this.mObjModel.Tag==''){
            this.bmlfonClearControl()
            return
          }
          this.isShowExtender = false;
          let reqData: any = {
            JSON: JSON.stringify({
              HeaderData: [{Tag: this.mObjModel.Tag}],
              Response: [{ResponseText:'', ErrorCode:''}]
            })
          };
          this.objService.bmlfonGet(reqData).subscribe(res => {
            if (res == null) {
              this.isShowExtender = true;
            }
            if (res.HeaderData.length > 0) {
              if (res.HeaderData[0].ItemKey == '') {
                this.objToastService.notifyError((this.objUtils.bmgfonTranslate('TAGENTRY.INVALIDTAG')));
                setTimeout(function () { document.getElementById('txtTagNo').focus(); }, 10);
                this.isShowExtender = true;
                return false;
              }else{
                this.mbCustomControl = true;
                this.msFormMode = "U";
                this.mbDisabledPrimaryKey = true;
                this.mObjModel = res.HeaderData[0];
                this.mObjModel.Qty =  + this.mObjModel.Qty
                this.mbChange = false;
                this.msCallFor='';
                this.isShowExtender = true;                
                //FiledExtender -- Start
                this.mskeyValue = this.mObjModel.Tag;
                // -- End
                  //let jsonObj = { Tag: this.mObjModel.Tag }
                //this.objUtils.refreshUrlAndInitCustom('/main/physicalcounting/refresh', '/main/physicalcounting/tag-entry/manage', true, jsonObj)
              }
              //Begin Format date/time columns in customization grid field to remove "T"  
              this.objUtils.bmlfTransformCustomFieldsGet(res.DetailData)
              //end Format date/time columns in customization grid field 
            } else {
              this.isShowExtender = true;
            }
            
          }, err => {
            this.isShowExtender = true;
          })
    this.bmlfonURL(this.msKey)
}

