import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { Observable } from 'rxjs';
import { ODataModel } from 'src/app/core/models/odata.model';

@Injectable({
  providedIn: 'root'
})
export class InitiateCount2Service {
  
  constructor(
    private httpExt: HttpCleintExtService,
    private appSettings: AppSettingsService    
     ) {
  }
 
  public bmlfonMobileInitiateCountTwo(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/MobileInitiateCountTwo`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmlfGETGeneralSqlData(headerData: any) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GETGeneralSqlData`;
    return this.httpExt.post<any>(url, headerData, null);
   
    }

}
