  bmlfonInitiate(){
    if (!this.objUtils.mbWrite) {
      this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };

    
    let pArrGrid1:any 
    pArrGrid1 = this.mArrGrid1.filter(x => x.Select == true );

    if(pArrGrid1.length==0){
      this.objToastService.notifyError(this.objUtils.bmgfonTranslate('INITIATECOUNT2.ATLEAST'));
      return false;
    }

    pArrGrid1.forEach(ele => {
      ele.QtyActual=Number( ele.QtyActual).toFixed(this.miQtyPrecision);
      ele.QtyonHand=Number( ele.QtyonHand).toFixed(this.miQtyPrecision);
      ele.QtyAdjusted=Number( ele.QtyAdjusted).toFixed(this.miQtyPrecision);
    });

    let reqData: any = {
      JSON: JSON.stringify({
        Details: pArrGrid1,
        Response: [{ResponseText:'',ErrorCode:''}]
      })
    };

      this.objService.bmlfonMobileInitiateCountTwo(reqData).subscribe(res => {
        if (res == null) {
          this.objToastService.notifyError(this.objUtils.bmgfonTranslate('COMMON.MSG.SERVERERROR'));
          return false;
        }
        if (res.Response[0].ErrorCode == "") {
          this.objToastService.notifySuccess(this.objUtils.bmgfonTranslate('COMMON.MSG.SAVE'));
          setTimeout(() => {
            this.router.navigate(['/main/dashboard/dashboardpage']);
          }, 1000);
          
          }
      })
  }

  bmlfOpenCustomLookupModal(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, pnumRowIdx = null) {
    
    this.mbIsLoading = true;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    if (psEvent == 'blur' && (psValue == undefined || psValue == '')) {
      this.mobjLookupDataModel[psField] = "";
      return false
    }

    if ((psField.toLowerCase().includes('custom') || psField.toLowerCase().includes('user'))) {
      if (psScreenName == 'DTLineItem') {
        this.mArrCustomLookutempData = this.mArrGrid1Col.filter(x => x.Field == psField && x.Type == 'lookup');
      } else if (psScreenName == 'DTPriceCustItem') {
        //this.mArrCustomLookutempData = this.mArrSalesKitGrid2Col.filter(x => x.Field == psField && x.Type== 'lookup' );
      }

      let json = { SqlQuery: this.mArrCustomLookutempData[0].SQL }
      this.objUtils.getCustomSqlData(json).subscribe(res => {
        this.mbIsLoading = false;
        if (res == null) {
          this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
          return false;
        }
        let obj1: any = {};
        if (Object.entries(res).length > 0 && res.LookupData.length > 0) {

          if (psField == this.mArrCustomLookutempData[0].Field) {
            psField = this.mArrCustomLookutempData[0].Title
          }

          this.marrLookupGridData = res.LookupData;
          this.mnumLineItemRowIndex = pnumRowIdx;
          this.mobjLookupKeyData.field = psField;
          this.mobjLookupKeyData.gridData = this.marrLookupGridData;
          this.mobjLookupKeyData.type = psType;
          this.mobjLookupKeyData.eleId = psEleId;
          if (psValue) {
            this.mobjLookupKeyData.searchValue = psValue;
          } else {
            this.mobjLookupKeyData.searchValue = '';
          }
          if (psEvent == 'blur' && psValue) {
            let parrFilterdArr: any = [];
            let pobjFirstInput = Object.keys(this.marrLookupGridData[0])[0];
            parrFilterdArr = this.marrLookupGridData.filter(obj => {
              if (obj[pobjFirstInput].toUpperCase() == psValue.toUpperCase()) {
                return obj;
              }
            });
            if (parrFilterdArr.length == 1) {
              this.mobjLookupKeyData.value = parrFilterdArr[0];
              this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
              this.bmlfSaveLookupField(this.mobjLookupKeyData);
              this.mbIsLookupModal = false;
            } else {
              this.mbIsLookupModal = true;
            }
          } else if (psEvent == 'click') {
            this.mbIsLookupModal = true;
          }

        } else {
          this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
        }
      }, err => {
        console.log(err);
        this.mbIsLoading = false;
      })
    }
  }

  bmlfOpenErrorBlock(psMsg): void {
    this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate(psMsg));
  }

  bmlfSaveLookupField(pobjEvent) {
    
    let pArrSelectRows:any=[]
    pArrSelectRows = pobjEvent.value;

    let pbDuplicate:any=false;
    let psStr:any=''
    if(pArrSelectRows.length>0){ //Check Duplicate Record
      pArrSelectRows.forEach(ele => {
      let parrDuplicateRecord = this.mArrGrid1.filter(x => x.BinNo.toString() == ele.BinNo.toString() && x.LotNo.toString() == ele.LotNo.toString() && x.ItemKey.toString() == ele.ItemKey.toString() && x.Location.toString() == ele.Location.toString())  ;
      if (parrDuplicateRecord.length >= 1) {
        if(ele.LotNo.toString()=='' && ele.BinNo.toString()==''){
          psStr='ItemKey: ' + ele.ItemKey.toString() +  ' Location: '+ ele.Location.toString();
        }else if(ele.LotNo.toString()=='' ){
          psStr='ItemKey: ' + ele.ItemKey.toString() +  ' Location: '+ ele.Location.toString() + ' BinNo: ' + ele.BinNo.toString();
        }else if(ele.BinNo.toString()==''){
          psStr='ItemKey: ' + ele.ItemKey.toString() +  ' Location: '+ ele.Location.toString() + ' LotNo: ' + ele.LotNo.toString();
        }else{
          psStr='ItemKey: ' + ele.ItemKey.toString() +  ' Location: '+ ele.Location.toString() + ' LotNo: ' + ele.LotNo.toString() + ' BinNo: ' + ele.BinNo.toString();
        }
        pbDuplicate=true;
      };
    });
  }
    if(pbDuplicate){
      this.objToastService.notifyError(this.objUtils.bmgfonTranslate('INITIATECOUNT2.DUPLICATE') + psStr);
      return false
    }


    if(pArrSelectRows.length>0){
      pArrSelectRows.forEach(ele => {
        const pobjDataAdd: any = Object.assign({}, this.mObjModelGrid1);
        pobjDataAdd.BinNo=ele.BinNo;
        pobjDataAdd.LotNo=ele.LotNo;
        pobjDataAdd.ContainerNo=ele.ContainerNo;
        pobjDataAdd.QtyActual=ele.QtyActual;
        pobjDataAdd.QtyonHand=ele.QtyonHand;
        pobjDataAdd.QtyAdjusted=ele.QtyAdjusted;
        pobjDataAdd.ItemKey=ele.ItemKey;
        pobjDataAdd.Description=ele.Description;
        pobjDataAdd.Location=ele.Location;
        pobjDataAdd.Seriallotflg=ele.Seriallotflg;
        pobjDataAdd.PhysicalCountOrdNo=ele.PhysicalCountOrdNo;
        this.mArrGrid1.push(pobjDataAdd);
      });
    }

    
    


    this.mbIsLookupModal = false;
         //Customization Grid Lookup Value Transfer Begin
         if ((pobjEvent.field.toLowerCase().includes('custom') || pobjEvent.field.toLowerCase().includes('user'))) {
          if (this.msLookupCallFor == "dtGroupData") {
            this.mArrGrid1[this.mnumLineItemRowIndex][pobjEvent.field] = pobjEvent.valueKey.trim();
            //this.mArrGrid1[this.mnumLineItemRowIndex].Flag = "M";
            if (this.mArrGrid1[this.mnumLineItemRowIndex].Flag == " " || this.mArrGrid1[this.mnumLineItemRowIndex].Flag == "" || this.mArrGrid1[this.mnumLineItemRowIndex].Flag == "U") {
              this.mArrGrid1[this.mnumLineItemRowIndex].Flag = "M";
            }
          }
       
          this.mbChange = true;
          return;
        }
        //Customization Grid Lookup Value Transfer End

  }

